<template>
  <div>
    <div class="url-list">
      <div v-if="urls.length === 0">No URLs found in the file.</div>
      <div v-else>
        <div class="pdf-scroll-container">
          <div class="pdf-container">
            <!-- Display PDF thumbnails -->
            <img
              v-for="(url, index) in urlsWithHttp"
              :key="index"
              :src="thumbnails[index]"
              alt="PDF thumbnail"
              class="pdf-thumbnail"
              @click="openPdf(url)"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as pdfjsLib from 'pdfjs-dist/webpack';
import 'pdfjs-dist/web/pdf_viewer.css';

export default {
  name: 'arrayPdf',
  props: {
    urls: {
      type: Array,
      required: true,
    },
  },
  data() {
    return {
      thumbnails: [],
    };
  },
  computed: {
    urlsWithHttp() {
      // Add "https://res.cloudinary.com/alvin-tan/image/upload/" prefix to each URL
      return this.urls.map((url) => 'https://res.cloudinary.com/alvin-tan/image/upload/' + url);
    },
  },
  methods: {
    async generateThumbnail(url) {
      const loadingTask = pdfjsLib.getDocument(url);
      const pdf = await loadingTask.promise;
      const page = await pdf.getPage(1);
      const viewport = page.getViewport({ scale: 0.5 });
      const canvas = document.createElement('canvas');
      const context = canvas.getContext('2d');
      canvas.height = viewport.height;
      canvas.width = viewport.width;

      const renderContext = {
        canvasContext: context,
        viewport: viewport,
      };
      await page.render(renderContext).promise;
      return canvas.toDataURL();
    },
    async loadThumbnails() {
      for (const url of this.urlsWithHttp) {
        const thumbnail = await this.generateThumbnail(url);
        this.thumbnails.push(thumbnail);
      }
    },
    openPdf(url) {
      window.open(url, '_blank');
    },
  },
  async mounted() {
    await this.loadThumbnails();
  },
};
</script>

<style>
.url-list {
  margin-top: 20px;
}

.pdf-scroll-container {
  overflow-x: auto;
  white-space: nowrap;
}

.pdf-container {
  display: inline-block;
}

.pdf-thumbnail {
  width: auto;
  height: 150px; /* Set a fixed height for the thumbnails */
  margin: 5px;
  cursor: pointer;
}
</style>
