<template>
  <div v-html="htmlContent" class="html-container"></div>
</template>

<script>
export default {
  name: 'HtmlDisp',
  props: {
    file: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      htmlContent: ''
    };
  },
  watch: {
    file: {
      immediate: true,
      handler(newFile) {
        this.fetchHtmlContent(newFile);
      }
    }
  },
  methods: {
    async fetchHtmlContent(file) {
      console.log(`Fetching HTML content for file: ${file}`);
      try {
        const response = await fetch(`/${file}`);
        console.log(`Fetch response status: ${response.status}`);
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        this.htmlContent = await response.text();
        console.log('HTML content fetched successfully');
      } catch (error) {
        console.error('There was a problem with the fetch operation:', error);
      }
    }
  },
  created() {
    console.log(`HtmlDisp component created with file: ${this.file}`);
  },
  mounted() {
    console.log('HtmlDisp component mounted');
  }
};
</script>

<style>
.html-container {
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
</style>
