<template>
    <div>
    
   
      <div class="url-list">
        <div v-if="urls.length === 0">No URLs found in the file.</div>
        <div v-else>
  
          <div class="image-scroll-container">
            <div class="image-container">
              <img v-for="(url, index) in urlsWithHttp" :key="index" :src="url" alt="image" class="image-item">
              <!-- <p v-for="(url, index) in urlsWithHttp" :key="index" class="url-item">{{ url }}</p> -->
      
            </div>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  // Import ref function from Vue
  
  export default {
    name: 'arrayImage',
    props: {
    
     
      urls: {
        type: Array,
        required: true
      }
    },
    computed: {
      urlsWithHttp() {
        // Add "https://res.cloudinary.com/alvin-tan/image/upload/v1717129154/" prefix to each URL
        return this.urls.map(url => "https://res.cloudinary.com/alvin-tan/image/upload/" +  url);
      }
    }
  };
  </script>
  
  <style>
  .url-list {
    margin-top: 20px;
  }
  
  .image-scroll-container {
    overflow-x: auto;
    white-space: nowrap;
  }
  
  .image-container {
    display: inline-block;
  }
  
  .image-item {
    width: auto;
    max-width: 100%;
    height: 300px; /* Set a fixed height for the images */
    margin: 5px;
    object-fit: contain;
  }
  </style>
  