<template>
    <div>
      <div class="url-list">
        <div v-if="urls.length === 0">No URLs found in the file.</div>
        <div v-else>
          <div class="video-scroll-container">
            <div class="video-container">
              <video v-for="(url, index) in urlsWithHttp" :key="index" :src="url" controls class="video-item"></video>
              <!-- <p v-for="(url, index) in urlsWithHttp" :key="index" class="url-item">{{ url }}</p> -->
            </div>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'arrayVideo',
    props: {
      urls: {
        type: Array,
        required: true
      }
    },
    computed: {
      urlsWithHttp() {
        console.log('Computing urlsWithHttp...');
        // Add "https://res.cloudinary.com/alvin-tan/video/upload/" prefix to each URL
        const prefixedUrls = this.urls.map(url => "https://res.cloudinary.com/alvin-tan/video/upload/" + url);
        console.log('Computed urlsWithHttp:', prefixedUrls);
        return prefixedUrls;
      }
    }
  };
  </script>
  
  <style>
  .url-list {
    margin-top: 20px;
  }
  
  .video-scroll-container {
    overflow-x: auto;
    white-space: nowrap;
  }
  
  .video-container {
    display: inline-block;
  }
  
  .video-item {
    width: auto;
    max-width: 100%;
    height: 300px; /* Set a fixed height for the videos */
    margin: 5px;
    object-fit: contain;
  }
  </style>
  